  $arrow-r: 95px 65px -95px -65px 12px 22px 120px 90px 'sprite.png';
  $icon-ca: 53px 0px -53px 0px 38px 38px 120px 90px 'sprite.png';
  $icon-che: 95px 0px -95px 0px 25px 16px 120px 90px 'sprite.png';
  $icon-close-red: 43px 53px -43px -53px 21px 20px 120px 90px 'sprite.png';
  $icon-full: 0px 0px 0px 0px 49px 49px 120px 90px 'sprite.png';
  $icon-k: 0px 53px 0px -53px 39px 37px 120px 90px 'sprite.png';
  $icon-stimul: 95px 43px -95px -43px 17px 18px 120px 90px 'sprite.png';
  $yo: 95px 20px -95px -20px 20px 19px 120px 90px 'sprite.png';

  @mixin sprite-position($sprite) {
    $sprite-offset-x: nth($sprite, 3);
    $sprite-offset-y: nth($sprite, 4);
    background-position: $sprite-offset-x  $sprite-offset-y;
  }

  @mixin sprite-image($sprite) {
    $sprite-image: nth($sprite, 9);
    background-image: url(%=static=%png-sprite/96dpi/#{$sprite-image});
  }

  @mixin sprite-mixin-template($sprite) {
    @include sprite-image($sprite);
    @include sprite-position($sprite);
    width: nth($sprite, 5);
    height: nth($sprite, 6);
  }

  @mixin bg($sprite, $repeat: no-repeat) {
    @include sprite-mixin-template($sprite);
    $sprite-total-width: nth($sprite, 7);
    $sprite-total-height: nth($sprite, 8);
    background-size: $sprite-total-width, $sprite-total-height;
    background-repeat: $repeat;



  }
